import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import { ExportToExcelService } from 'src/app/core/services/common/export-to-excel.service';
import { OfficeExpenseService } from 'src/app/core/services/office-expense/office-expense.service';
import { PagerService } from 'src/app/core/services/pagination/pager.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-manage-office-expenses',
  templateUrl: './manage-office-expenses.component.html',
  styleUrls: ['./manage-office-expenses.component.scss'],
})
export class ManageOfficeExpensesComponent implements OnInit {
  arrExpense: any = [];
  arrExpenses: any = [];

  // pagination
  pager: any = {};
  intTotalCount = 0;
  intPageLimit = 50;
  intSkipCount = 0;
  fetchLoading = false;

  myForm: FormGroup;

  constructor(
    private officeExpenseService: OfficeExpenseService,
    private formBuilder: FormBuilder,
    private exportToExcelService: ExportToExcelService,
    private pageServiceObj: PagerService
  ) {
    this.myForm = this.formBuilder.group({
      expenseType: [null],
      fromDate: [''],
      toDate: [''],
    });
    this.getMetaData();
  }

  getMetaData() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
    };

    this.officeExpenseService.getExpenseType(obj).subscribe((data) => {
      if (data.success) {
        this.arrExpenses = data.data;
      } else {
        this.arrExpenses = [];
      }
    });
  }

  ngOnInit() {
    this.getAllExpense();
  }

  setPage(page) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.pageServiceObj.getPager(
      this.intTotalCount,
      page,
      this.intPageLimit
    );
    this.getAllExpense();
  }

  getFromCount() {
    return this.intPageLimit * (this.pager.currentPage - 1) + 1;
  }

  getToCount() {
    return (
      this.intPageLimit * (this.pager.currentPage - 1) + this.arrExpense.length
    );
  }
  getAllExpense() {
    console.log('first');
    let skipCount = this.intSkipCount;
    if (this.pager.intSkipCount) {
      skipCount = this.pager.intSkipCount;
    }
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: skipCount,
      intPageLimit: this.intPageLimit,
      // fkCustomerId: '',
      // cust_Code: this.myForm.value.customer,
      fkExpenseTypeId: this.myForm.value.expenseType?._id || '',
      startDate: this.myForm.value.fromDate,
      endDate: this.myForm.value.toDate,
    };

    this.fetchLoading = true;
    this.officeExpenseService.getOfficeExpense(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrExpense = res.data;

        this.intTotalCount = res.count;

        this.pager = this.pageServiceObj.getPager(
          this.intTotalCount,
          this.pager.currentPage,
          this.intPageLimit
        );
      } else {
        this.arrExpense = [];
        // alert(res.message);
      }
    });
  }

  downloadExcel() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
      fkCustomerId: '',
      fkExpenseTypeId: this.myForm.value.fkExpenseTypeId._id,
      cust_Code: this.myForm.value.customer,
      fromDate: this.myForm.value.fromDate,
      endDate: this.myForm.value.toDate,
    };

    this.fetchLoading = true;
    this.officeExpenseService.getOfficeExpense(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        let jsonData: any = [];

        res.data.forEach((e) => {
          const cost_Details = e.cost_Details
            .map((expense) => `${expense.name}: ${expense.amount}`)
            .join(', ');

          let obj: any = {
            'Customer Code': e.cust_Code,
            'Customer Name': e.strName,
            Email: e.strEmail,
            Phone: e.phone,
            Country: e.country,
            'Amount per Person': e.per_Person,
            'Number of Person': e.no_Of_Persons,
            VAT: e.vat,
            'Total Amount': e.Total_Amount,
            'Cost Price': e.cost_Price,
            'Refund Amount': e.Refund_Amount,
            'G-Voucher Credit Amount': e.Service_Credit_Amount,
            'G-Voucher Debit Amount': e.Service_Debit_Amount,
            'Profit Price': e.profit_Price,
            Package: e.strPackage,
            'Travel Date': moment(e.travel_Date).format('DD-MM-YYYY'),
            Description: e.strDescription,
            'Cost Details': cost_Details,
            'Created On': moment(e.createdAt).format('DD-MM-YYYY'),
          };
          jsonData.push(obj);
        });

        this.exportToExcelService.exportAsExcelFile(jsonData, 'Customers ');
      }
    });
  }

  deleteCustomer(id) {
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#d33',
      reverseButtons: true,
      confirmButtonText: 'Yes, delete it!',
    }).then((result) => {
      if (result.isConfirmed) {
        const obj = {
          strLoginUserId: localStorage.getItem('userId'),
          fkExpenseId: id,
        };

        this.officeExpenseService.deleteOfficeExpense(obj).subscribe((res) => {
          if (res.success) {
            Swal.fire({
              title: 'Deleted!',
              text: 'Your file has been deleted.',
              icon: 'success',
            });
            this.getAllExpense();
          } else {
            // alert(res.message);
          }
        });
      }
    });
  }
}
