import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { ApiResponse } from '../../model/api-response.models';
import { catchError, retry } from 'rxjs';
import { CommonService } from '../common/common.service';

@Injectable({
  providedIn: 'root',
})
export class OfficeExpenseService {
  private apiURL: string = environment.API_ENDPOINT;

  constructor(private http: HttpClient, private commonService: CommonService) {}

  httpOptions = {
    headers: new HttpHeaders({
      Authorization: 'bearer ' + localStorage.getItem('token'),
    }),
  };

  addOfficeExpense(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'expense/expense_create',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  getOfficeExpense(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'expense/get_all_expense',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  getExpenseType(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'expense/get_expense_types',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  updateOfficeExpense(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'expense/expense_update',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  deleteOfficeExpense(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'expense/expense_delete',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
}
