import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { ToastrService } from 'ngx-toastr';
import { OfficeExpenseService } from 'src/app/core/services/office-expense/office-expense.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-update-office-expenses',
  templateUrl: './update-office-expenses.component.html',
  styleUrls: ['./update-office-expenses.component.scss'],
})
export class UpdateOfficeExpensesComponent {
  myForm: FormGroup;
  submitted: boolean = false;
  fkExpenseId = '';
  arrExpenses = [];

  constructor(
    private activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private officeExpenseService: OfficeExpenseService,
    private toastr: ToastrService,
    private readonly http: HttpClient,
    private router: Router
  ) {
    activatedRoute.params.subscribe((params) => {
      this.fkExpenseId = params.id;
    });
    this.myForm = this.formBuilder.group({
      strExpense: [null, Validators.required],
      Amount: ['', Validators.required],
      tranferType: ['Debit', Validators.required],
      expense_Date: ['', Validators.required],
    });
    this.getMetaData();
  }
  getMetaData() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
    };

    this.officeExpenseService.getExpenseType(obj).subscribe((data) => {
      if (data.success) {
        this.arrExpenses = data.data;
        this.getAllExpense();
      } else {
        this.arrExpenses = [];
      }
    });
  }
  get f() {
    return this.myForm.controls;
  }

  getAllExpense() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1,
      fkExpenseId: this.fkExpenseId,
      // cust_Code: this.myForm.value.customer,
      startDate: this.myForm.value.fromDate,
      endDate: this.myForm.value.toDate,
    };

    this.officeExpenseService.getOfficeExpense(obj).subscribe((res) => {
      if (res.success) {
        this.myForm.patchValue({
          strExpense: res.data[0].strExpense,
          Amount: res.data[0].Amount,
          tranferType: res.data[0].tranfer_Type,
          expense_Date: moment(res.data[0].expense_Date).format('YYYY-MM-DD'),
        });
      } else {
        this.toastr.error('Error While fetching data');

        // alert(res.message);
      }
    });
  }
  resetForm() {
    this.myForm.reset();
    this.submitted = false;
  }
  onSubmit() {
    this.submitted = true;

    if (this.myForm.invalid) {
      return;
    }

    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      fkExpenseId: this.fkExpenseId,
      expense_Date: this.myForm.value.expense_Date,
      fkExpenseTypeId: this.myForm.value.strExpense._id,
      strExpense: this.myForm.value.strExpense.expense,
      Amount: this.myForm.value.Amount,
      tranferType: this.myForm.value.tranferType,
    };

    this.officeExpenseService.updateOfficeExpense(obj).subscribe((res) => {
      if (res.success) {
        Swal.fire({
          title: 'Success',
          text: 'Successfully Updated Expense',
          icon: 'success',
        }).then((value) => {
          if (value) {
            this.myForm.reset();
            this.submitted = false;
            this.router.navigate(['/office-expenses']);
          }
        });
      } else {
        Swal.fire({
          title: 'Error',
          text: res.message,
          icon: 'error',
        });
      }
    });
  }
}
