import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { OfficeExpenseService } from 'src/app/core/services/office-expense/office-expense.service';
import { SupplierService } from 'src/app/core/services/supplier/supplier.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-create-office-expenses',
  templateUrl: './create-office-expenses.component.html',
  styleUrls: ['./create-office-expenses.component.scss'],
})
export class CreateOfficeExpensesComponent {
  myForm: FormGroup;
  submitted: boolean = false;
  arrExpenses = [];

  constructor(
    private formBuilder: FormBuilder,
    private officeExpenseService: OfficeExpenseService,
    private readonly http: HttpClient,
    private router: Router
  ) {
    this.myForm = this.formBuilder.group({
      strExpense: [null, Validators.required],
      Amount: ['', Validators.required],
      tranferType: ['Debit', Validators.required],
      expense_Date: ['', Validators.required],
    });
    this.getMetaData();
  }

  get f() {
    return this.myForm.controls;
  }

  getMetaData() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
    };

    this.officeExpenseService.getExpenseType(obj).subscribe((data) => {
      if (data.success) {
        this.arrExpenses = data.data;
      } else {
        this.arrExpenses = [];
      }
    });
  }

  resetForm() {
    this.myForm.reset();
    this.submitted = false;
  }
  onSubmit() {
    this.submitted = true;

    if (this.myForm.invalid) {
      return;
    }

    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      expense_Date: this.myForm.value.expense_Date,
      fkExpenseTypeId: this.myForm.value.strExpense._id,
      strExpense: this.myForm.value.strExpense.expense,
      Amount: this.myForm.value.Amount,
      tranferType: this.myForm.value.tranferType,
    };

    this.officeExpenseService.addOfficeExpense(obj).subscribe((res) => {
      if (res.success) {
        Swal.fire({
          title: 'Success',
          text: 'Successfully Created Expense',
          icon: 'success',
        }).then((value) => {
          if (value) {
            this.myForm.reset();
            this.submitted = false;
            this.router.navigate(['/office-expenses']);
          }
        });
      } else {
        Swal.fire({
          title: 'Error',
          text: res.message,
          icon: 'error',
        });
      }
    });
  }
}
