import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { NgbTooltipModule } from '@ng-bootstrap/ng-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';
import { TranslateModule } from '@ngx-translate/core';
import { InlineSVGModule } from 'ng-inline-svg-2';
import { WidgetsModule } from 'src/app/theme/partials';
import { CreateOfficeExpensesComponent } from './create-office-expenses/create-office-expenses.component';
import { ManageOfficeExpensesComponent } from './manage-office-expenses/manage-office-expenses.component';
import { UpdateOfficeExpensesComponent } from './update-office-expenses/update-office-expenses.component';

@NgModule({
  declarations: [
    ManageOfficeExpensesComponent,
    CreateOfficeExpensesComponent,
    UpdateOfficeExpensesComponent,
  ],
  imports: [
    CommonModule,
    NgbTooltipModule,
    FormsModule,
    NgSelectModule,
    InlineSVGModule,
    ReactiveFormsModule,
    TranslateModule,
    RouterModule.forChild([
      {
        path: '',
        component: ManageOfficeExpensesComponent,
      },
      {
        path: 'create',
        component: CreateOfficeExpensesComponent,
      },
      {
        path: 'update/:id',
        component: UpdateOfficeExpensesComponent,
      },
    ]),
    WidgetsModule,
  ],
})
export class OfficeExpensesModule {}
